import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Departement from '@/components/Departement'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/departement/:nom',
      name: 'Departement',
      component: Departement
    }
  ]
})



